import React, { useState } from 'react';
import instance from '../../base_url.js';
const Services = () => {
    const [cat, setCat] = useState([]);
    const getCat = async () => {
        await instance
            .post('main_home/get_service_cat', {
                key: '7fbaf493e3fe38a48934d93a3168926018adb657',
            })
            .then((response) => {
                if (cat.length == 0) {
                    setCat(response.data);
                }
            });
    };
    getCat();
    return (
        <div className="main__home__services">
            <div className="container">
                <div className="main__home__title__wrapper">
                    <span className="main__home__title">
                        Our Services Categories
                    </span>
                </div>
                <div className="row">
                    <div className="row">
                        {cat.map((val, key) => {
                            return (
                                <div className="col-6 col-md-3 col-lg-2">
                                    <div className="main__home__service">
                                        <img
                                            src={'../../' + val.cat_image}
                                            alt={val.alt}
                                            className="main__home__service__img"
                                        />
                                        <h6 className="main__home__service__name">
                                            {val.cat_title}
                                        </h6>
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Services;
